import React from 'react';
import { Link } from 'gatsby';
import get from 'lodash/get';

import Typography from './Typography';

import withTheme from '../utils/withTheme';

const PostTeaser = ({ hasExcerpt, node, theme }) => {
  const title = get(node, 'frontmatter.title') || node.fields.slug;
  return (
    <div className="post-teaser" key={node.fields.slug}>
      <Typography is="h3" className="post-teaser-title">
        <Link to={node.fields.slug}>{title}</Link>
      </Typography>
      <small className="post-teaser-date">{node.frontmatter.date}</small>
      {hasExcerpt && (
        <Typography
          className="post-teaser-excerpt"
          dangerouslySetInnerHTML={{ __html: node.excerpt }}
        />
      )}
      <style jsx>{`
        .post-teaser-title {
          margin-bottom: 4px;
          color: ${theme.colors.primary};
        }

        .post-teaser-date {
          color: #757575;
        }

        .post-teaser-excerpt {
          margin: 0;
        }
      `}</style>
    </div>
  );
};

export default withTheme(PostTeaser);
